.mastheader {
  padding: 170px 0 150px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
  overflow: hidden;
  background-color: #f4f4f5;
}

.mastheader .mastheader_iphone {
  position: absolute;
  bottom: -310px;
  left: 50%;
  margin-left: -185px;
}

.mastheader .video-btn {
  margin-top: 48px;
}

.mastheader .video-btn i {
  margin-right: 15px;
}

.mastheader:after {
  content: '';
  display: block;
  width: 60%;
  height: 140%;
  position: absolute;
  background: #212c5f;
  top: -120px;
  right: -220px;
  z-index: 3;
  transform: rotate(13deg);
}

.mastheader:before {
  content: '';
  display: block;
  width: 60%;
  height: 140%;
  position: absolute;
  background: #c82343;
  top: -120px;
  right: -210px;
  z-index: 2;
  transform: rotate(18deg);
}

.h2 {
  font-size: 50px;
  font-weight: 800 !important;
  color: #6f7179;
}
.span {
  font-size: 12px;
  line-height: 15px;
  display: block;
  color: #626262;
}

.flex-right {
  display: flex;
  justify-content: flex-end;
}

.whitelabel {
  min-width: 420px;
  background: #fff;
  border-radius: 7px;
  padding: 20px;
  box-shadow: 0px 5px 15px -5px rgba(0, 0, 0, 0.22);
  z-index: 4;
  position: relative;
  
  .title {
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
    align-items: center;
    .h4 {
      font-family: Lato;
      font-size: 20px;
      color: #303036;
    }
    .time {
      text-align: right;
    }
  }
}


.two-items {
  width: 100%;
  display: flex;
  justify-content: space-between;
  & > div {
    width: 100%;
    margin: 15px 5px 0;
  }
}

.actions {
  padding: 15px 5px;
}

.footer {
  padding-top: 10px;
  .security {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .tagSecurity {
    display: flex;
    align-items: center;
    justify-content: center;
    .p {
      font-size: 14px;
      text-transform: uppercase;
      line-height: 15px;
      font-weight: 600;
      font-family: Lato;
      color: #222d5e;
      margin: 0;
      margin-left: 5px;
    }
    .icon {
      width: 40px;
      height: 40px;
      background: #222d5e;
      border-radius: 40px;
      background: url('../Commons/sprite.png') 3px -99px;
    }
    
  }
}

.reserva {
  display: flex;
  flex-direction: column;
  padding-top: 15px;
}

.reserva .p {
  font-size: 16px;
  margin: 0;
  font-family: Lato;
}

.reserva .h4 {
  font-family: Lato;
  font-weight: 600;
  color: #625f5f;
}

.bg-header {
  width: 100%;
  position: absolute;
  height: 100%;
  background: url(./bg-header.svg) left bottom no-repeat;
  top: 0;
  background-size: 80%;
  opacity: .15;
  z-index: 0;
}

@media(max-width: 768px) {
  .whitelabel {
    min-width: 100%;
    margin-top: 25px;
  }
  .mastheader {
    padding: 130px 0 70px;

    .h2 {
      font-size: 35px;
      line-height: 40px
    }
    .p {
      font-size: 19px;
    }
  }
}